.presentation {
  padding-bottom: 32px;
}

.presentation__slogan {
  color: rgba(0, 0, 0, 1);
  font-size: 18px;
  font-weight: 700;
  font-style: normal;
  line-height: 26px;
  margin: 0;
  padding-bottom: 35px;
  text-transform: uppercase;
  text-align: center;
}

.presentation__intro {
  color: rgba(51, 51, 51, 1);
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  padding-bottom: 32px;
  text-transform: uppercase;
  text-align: center;
  margin: 0 auto;
}

.presentation__list {
  list-style-type: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  padding-right: 0;
}

.presentation__detail {
  list-style-type: none;
}

.presentation__container {
  background-color: #81b3d2;
  color: #ffffff;
  text-transform: uppercase;
}

.presentation__title-blue {
  margin: 0 auto;
  padding-top: 54px;
  padding-bottom: 11px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
}

.presentation__container .presentation__number {
  text-align: center;
  font-weight: 700;
  font-style: normal;
  text-transform: uppercase;
  font-size: 14px;
  box-sizing: border-box;
  line-height: 21px;
  align-items: center;
}

.presentation__container .presentation__information {
  text-align: center;
  font-style: normal;
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  padding-bottom: 40px;
  margin: 17px 22px 0 16px;
}

.presentation__header {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 1);
  padding-bottom: 16px;
  margin: 0 auto;
}

.presentation__paragraph {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 1);
  padding-bottom: 25px;
  margin: 0 auto;
}

.presentation__list .presentation__item--4 {
  box-shadow: 0 1px 0 rgb(1 1 1 / 20%);
}

.presentation__item--4 {
  .presentation__information {
    color: #000000;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
    padding-bottom: 24px;
    margin: 0 auto 1px auto;
  }
  background-color: #eeeeee;
}

.presentation__detail-item--3 .presentation__paragraph {
  padding-bottom: 52px;
}

.presentation__item--5 {
  .presentation__information {
    color: #000000;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
    padding-bottom: 24px;
    margin: 0 auto 1px auto;
    box-shadow: 0 1px 0 rgb(1 1 1 / 20%);
  }
  background-color: #eeeeee;
}

.presentation__item--6 {
  .presentation__information {
    color: #000000;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
    padding-bottom: 24px;
    margin: 0 auto;
  }
  background-color: #eeeeee;
}

.presentation__title {
  font-weight: 700;
  font-size: 21px;
  line-height: 15px;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
  padding-top: 26px;
  margin: 0 auto;
  padding-bottom: 20px;
}

.presentation__number {
  text-align: center;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 21px;
  padding-bottom: 17px;
  display: block;
  align-items: center;
}

.presentation__images-item {
  padding-bottom: 25px;
}
